.inputStyle {
    border: 1px solid #BFBFBF;
    border-radius: 100px;
    background: #FFFFFF;
    padding: 12px 0px 12px 25px;
    color: #141414;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    font-family: 'Nunito', sans-serif;
    width: 100%;

    @media (max-width: 1000px) {
        font-size: 14px;
        line-height: 22px;
    }

    &:focus {
        outline: none;
        border: 1px solid #00ABBC;
        ;
    }
}