.section-footer {
    .footer-body {
        padding: 30px 20px 49px 20px;
        display: flex;
        width: 100%;

        .logo-text-min-width {
            display: none;
            color: #595959;
        }

        p {
            font-size: 14px;
            line-height: 19.1px;
            margin: 0 0 10px 0;
        }

        .logo-container {
            .logo-img {
                height: 95px;
                width: 170px;
                margin-bottom: 30px;

                img {
                    height: 100%;
                    width: 100%;
                }
            }
        }

        .container-main {
            display: flex;
            justify-content: space-between;
            width: 100%;

            .footer-container-fist {
                padding-left: 10px;
                width: 100%;
                display: flex;
                justify-content: space-around;
            }

            .footer-container-second {
                width: 100%;
                display: flex;
                justify-content: space-around;

                .social-media-container {

                    ul {
                        list-style: none;

                        li {
                            display: flex;
                            align-items: center;
                            margin-bottom: 10px;

                            a {
                                font-size: 16px;
                                font-weight: 600;
                                line-height: 21.82px;
                                margin-left: 10px;
                                text-decoration: underline;
                                color: #141414;

                                &:hover {
                                    color: #00D2E7;
                                    cursor: pointer;
                                }
                            }
                        }
                    }
                }

                .contacts-container {
                    ul {
                        list-style: none;

                        .email {
                            justify-content: space-between;
                            text-decoration: underline;
                        }

                        li {
                            display: flex;
                            align-items: center;
                            margin-bottom: 10px;
                            justify-content: end;



                            a {
                                font-size: 16px;
                                font-weight: 500;
                                line-height: 21.82px;
                                margin-left: 10px;
                                color: #141414;

                                &:hover {
                                    color: #00D2E7;
                                    cursor: pointer;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 1000px) {


    .section-footer {
        .footer-body {
            justify-content: space-between;

            .container-main {

                .footer-container-fist {
                    flex-direction: column;
                    margin-left: 50px;

                    .rules-container {
                        margin-bottom: 20px;
                    }
                }

                .footer-container-second {
                    flex-direction: column;

                    .social-media-container {
                        margin-bottom: 20px;

                        ul {
                            display: flex;

                            li {

                                &:not(:first-child) {
                                    margin-left: 10px;
                                }


                                a {
                                    display: none;
                                }
                            }
                        }
                    }

                    .contacts-container {


                        ul {
                            .email {
                                justify-content: start;
                            }

                            li {
                                justify-content: start;

                                .numbers {
                                    padding-left: 24px;
                                }

                                a {
                                    font-size: 14px;
                                    line-height: 22px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 582px) {
    .section-footer {
        .footer-body {
            .logo-container {
                .logo-img {
                    width: 100px;
                    height: 55px;
                }

                ;

                p {
                    display: none;
                }
            }

            flex-direction: column;

            .logo-text-min-width {
                display: block;
            }

            .container-main {
                flex-direction: column-reverse;

                .footer-container-fist {
                    flex-direction: column;
                    padding-left: 0px;
                    margin-bottom: 20px;
                    margin-left: 0px;

                    .rules-container {}
                }

                .footer-container-second {

                    .social-media-container {
                        margin-bottom: 20px;

                        ul {
                            display: flex;

                            li {

                                &:not(:first-child) {
                                    margin-left: 10px;
                                }


                                a {
                                    display: none;
                                }
                            }
                        }
                    }

                    .contacts-container {

                        margin-bottom: 20px;

                        ul {
                            .email {
                                justify-content: start;
                            }

                            li {
                                justify-content: start;


                                a {
                                    font-size: 14px;
                                    line-height: 22px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

}