    .our-nanny {
        background-color: #F7FFFF;


        .slick-dots {
            list-style-type: none;

            li {
                margin: 0 2px 0 0;

                button {
                    background: #A1F1F9;
                    border-radius: 50px;
                    height: 10px;
                    width: 10px;
                    transition: 0.3s;
                    margin: 0 auto;

                    &::before {
                        content: '';
                        height: 0;
                        width: 0;
                    }

                    &:hover {
                        background: linear-gradient(83.08deg, #02A6CA 11.02%, #00D2E7 86.53%);
                    }

                    text-indent: -9999px;


                }
            }

            li.slick-active button {

                background: linear-gradient(83.08deg, #02A6CA 11.02%, #00D2E7 86.53%);
                border-radius: 10px;
                height: 10px;
                width: 22px;
                transition: 0.3s;
            }
        }

        .our-nanny-body {
            .our-nanny-header {
                margin-top: 120px;
                margin-bottom: 50px;

                h2 {
                    span {
                        position: relative;
                        z-index: 1;

                        &::after {
                            display: block;
                            position: absolute;
                            left: -3px;
                            top: 63px;
                            content: '';
                            width: calc(100%);
                            height: 15px;
                            background-color: #FFD583;
                            ;
                            z-index: -1;
                            border-radius: 100px;
                        }
                    }

                    img {
                        position: absolute;
                        left: 88px;
                        top: 9px;
                        transform: rotate(35.07deg);
                    }
                }
            }
        }
    }



    @media (max-width: 1000px) {
        .our-nanny {
            .our-nanny-body {
                .our-nanny-header {
                    margin-top: 50px;
                    margin-bottom: 30px;

                    h2 {
                        span {
                            position: relative;

                            &::after {
                                display: block;
                                position: absolute;
                                left: -3px;
                                top: 42px;
                                content: '';
                                width: calc(105%);
                                height: 10px;
                                background-color: #FFD583;
                                ;
                                z-index: -1;
                                border-radius: 100px;
                            }
                        }

                        img {
                            position: absolute;
                            left: 52px;
                            top: -3px;
                        }
                    }
                }
            }
        }

    }

    .arrow-next-nannies {
        position: absolute;
        top: -90px;
        right: 30px;

        @media (max-width: 887px) {
            display: none;
        }

    }

    .arrow-prev-nannies {
        position: absolute;
        top: -90px;
        right: 90px;
        left: auto;

        @media (max-width: 887px) {
            display: none;
        }

    }