.slider-section {
    background-color: #F7FFFF;

    .slick-track {
        display: flex !important;
        align-items: center !important;
    }

    .slick-dots {
        list-style-type: none;
        bottom: 15px !important;

        li {
            margin: 0 2px 0 0;

            button {
                // border-radius: 50%;
                background: #A1F1F9;
                border-radius: 50px;
                height: 10px;
                width: 10px;
                transition: 0.3s;
                margin: 0 auto;

                &::before {
                    content: '';
                    height: 0;
                    width: 0;
                }

                &:hover {
                    background: linear-gradient(83.08deg, #02A6CA 11.02%, #00D2E7 86.53%);
                }

                text-indent: -9999px;


            }
        }

        li.slick-active button {

            background: linear-gradient(83.08deg, #02A6CA 11.02%, #00D2E7 86.53%);
            border-radius: 10px;
            height: 10px;
            width: 22px;
            transition: 0.3s;
        }
    }

    .slide-container {
        display: flex;
        position: relative;
        align-items: center;

        .slide-part-one {

            .slider-title {
                margin-top: 0;
                margin-bottom: 35px;
                line-height: 73px;
                font-weight: 700;
                font-family: "VAG World";

                .text-decoration-yellow {
                    position: relative;
                    z-index: 2;

                    .img {
                        position: absolute;
                        top: 15px;
                        left: -25px;
                    }

                    &::after {
                        display: block;
                        position: absolute;
                        left: -5px;
                        top: 65px;
                        content: '';
                        width: calc(102%);
                        height: 16px;
                        background-color: #FFD583;
                        z-index: -1;
                        border-radius: 100px;
                    }
                }

                .text-decoration-pink {
                    position: relative;
                    z-index: 2;

                    .img {
                        position: absolute;
                        top: 15px;
                        left: -25px;
                    }

                    &::after {
                        display: block;
                        position: absolute;
                        left: -5px;
                        top: 65px;
                        content: '';
                        width: calc(102%);
                        height: 16px;
                        background-color: #ED82C9;
                        z-index: -1;
                        border-radius: 100px;
                    }
                }


                //text-decoration
                .text-decoration-blue {
                    position: relative;
                    z-index: 2;

                    .img {
                        position: absolute;
                        top: 15px;
                        left: -25px;
                    }

                    &::after {
                        display: block;
                        position: absolute;
                        left: -5px;
                        top: 65px;
                        content: '';
                        width: calc(102%);
                        height: 16px;
                        background-color: #00D2E7;
                        z-index: -1;
                        border-radius: 100px;
                    }
                }
            }


        }

        .slide-part-two {
            display: flex;
            position: relative;
            height: 100%;

            .image-one {
                position: relative;

                img {
                    width: 100%;
                    height: 100%;
                }

                .text-slider-one-first {
                    position: absolute;
                    top: 10px;
                    left: -160px;
                    font-weight: 500;
                    max-width: 350px;

                    p {
                        padding: 16px 20px;
                        background: #FFFFFF;
                        box-shadow: 0px 3px 10px rgba(32, 116, 124, 0.19);
                        border-radius: 15px 15px 0px 15px;
                    }
                }

                .text-slider-one-second {
                    position: absolute;
                    top: 55%;
                    left: 0px;
                    font-weight: 500;
                    max-width: 300px;



                    p {
                        padding: 16px 20px;
                        background: #FFFFFF;
                        box-shadow: 0px 3px 10px rgba(32, 116, 124, 0.19);
                        border-radius: 15px 15px 0px 15px;
                    }
                }

                .text-slider-two {
                    position: absolute;
                    top: 9px;
                    left: -90px;
                    font-weight: 500;
                    max-width: 320px;

                    @media (max-width: 1050px) {
                        top: 0;
                        left: -60px;
                        max-width: 250px;
                    }

                    p {
                        padding: 16px 20px;
                        background: #FFFFFF;
                        box-shadow: 0px 3px 10px rgba(32, 116, 124, 0.19);
                        border-radius: 15px 15px 0px 15px;

                        @media (max-width: 1050px) {
                            padding: 3px 7px;
                        }
                    }
                }

                .text-slider-three {
                    position: absolute;
                    bottom: 135px;
                    left: -210px;
                    font-weight: 500;
                    max-width: 500px;

                    p {
                        padding: 16px 20px;
                        background: #FFFFFF;
                        box-shadow: 0px 3px 10px rgba(32, 116, 124, 0.19);
                        border-radius: 15px 15px 0px 15px;
                    }
                }
            }

            .text-slider-four {
                position: absolute;
                top: -70px;
                left: 50px;
                font-weight: 500;
                max-width: 336px;

                p {
                    padding: 16px 20px;
                    background: #FFFFFF;
                    box-shadow: 0px 3px 10px rgba(32, 116, 124, 0.19);
                    border-radius: 15px 15px 0px 15px;
                }

            }

            .text-slider-five-first {
                position: absolute;
                top: -50px;
                left: 170px;
                font-weight: 500;
                max-width: 350px;

                @media (max-width: 480px) {
                    max-width: 300px;
                    left: 0;
                    top: -70px;
                }

                p {
                    padding: 16px 20px;
                    background: #FFFFFF;
                    box-shadow: 0px 3px 10px rgba(32, 116, 124, 0.19);
                    border-radius: 15px 15px 0px 15px;
                }
            }

            .text-slider-five-second {
                position: absolute;
                top: 63%;
                left: -80px;
                font-weight: 500;
                max-width: 300px;

                @media (max-width: 480px) {
                    max-width: 270px;
                    left: 0;
                    top: -70px;
                }

                p {
                    padding: 16px 20px;
                    background: #FFFFFF;
                    box-shadow: 0px 3px 10px rgba(32, 116, 124, 0.19);
                    border-radius: 15px 15px 0px 15px;
                }
            }

            .image-two {
                img {
                    width: 100%;
                    height: 100%;
                }
            }
        }

        .slider-btn {
            margin-bottom: 60px;
        }
    }
}


