@mixin card($border, $bg, $bs, $dots) {
    border: 1px solid $border;
    border-radius: 100px;
    background: $bg;
    border-radius: 10px;
    padding: 40px 30px;
    max-width: 350px;
    margin: 0 auto;

    &:hover {
        box-shadow: $bs;
        cursor: pointer;
    }

    .cardImg {
        margin-bottom: 30px;

        @media (max-width: 1000px) {
            margin-bottom: 15px;
        }
    }



    .title {
        font-family: "VAG World";
        margin-bottom: 30px;

        @media (max-width: 1000px) {
            margin-bottom: 15px;
        }
    }

    .list {
        font-size: 16px;
        line-height: 26px;
        color: #141414;
        list-style: none;
        padding: 0;
        margin-bottom: 30px;

        @media (max-width: 1000px) {
            font-size: 14px;
            line-height: 22px;
            margin-bottom: 15px;
        }

        .listItem::before {
            content: "\2022";
            color: $dots;
            height: 10px;
            width: 10;
            margin-right: 10px;
        }
    }
}



.cardPrimary1 {
    @include card(#D0F8FC, #F5FDFE, 0px 15px 33px rgba(37, 194, 210, 0.15), #00D2E7)
}

.cardPrimary2 {
    @include card(#FFEECD, #FFFCF5, 0px 15px 33px rgba(255, 173, 97, 0.15), #F5C362)
}

.cardSecondary {
    @include card(#FED4F0, #FFF5FC, 0px 15px 33px rgba(233, 99, 188, 0.15), #DF51AF)
}