.section-header {
    background-color: #F7FFFF;

    .header {
        display: flex;


        .header-body {
            display: flex;
            justify-content: space-between;
            width: 100%;
            align-items: center;
            margin: 20px 0 0 0;
            ;


            .header-content {
                display: flex;
                align-items: center;

                .header-list-sections {
                    display: flex;
                    list-style-type: none;
                    margin-right: 36px;
                    font-size: 16px;
                    font-weight: 500;
                    line-height: 21.82px;
                    overflow-x: auto;

                    .header-list-section {

                        padding: 25px 0;

                        &:not(:last-child) {
                            margin-right: 40px;
                        }

                        a {
                            cursor: pointer;
                            color: #141414;
                            text-decoration: none;
                            position: relative;
                            min-height: 50px;

                            // span {
                            //     position: absolute;
                            //     width: 28px;
                            //     height: 0px;
                            //     left: 16px;
                            //     top: 29px;

                            //     /* Primary 1/600 */

                            //     border: 3px dashed #00D2E7;
                            // }

                            &:hover {
                                &::after {
                                    position: absolute;
                                    bottom: -22px;
                                    left: 0;
                                    right: 0;
                                    top: 10px;
                                    display: block;
                                    margin: auto;
                                    text-align: center;
                                    content: '';
                                    height: 3px;
                                    background-color: #00D2E7;
                                    width: 28px;
                                    z-index: 2;
                                    border-radius: 5px;
                                }
                            }
                        }
                    }
                }

                .header-list-icons {
                    margin: 0 auto;
                    display: flex;
                    list-style-type: none;
                    margin-right: 30px;

                    .header-list-icon {
                        cursor: pointer;

                        &:not(:last-child) {
                            margin-right: 10px;
                        }
                    }
                }

                .header-btn {
                    display: flex;
                    align-items: center;
                }
            }
        }
    }
}

@media (max-width: 1047px) {
    .section-header {
        .header {
            .header-body {

                .header-content {

                    .header-list-sections {
                        margin-right: 10px;


                        .header-list-section {

                            &:not(:last-child) {
                                margin-right: 25px;
                            }
                        }
                    }

                    .header-list-icons {
                        margin-right: 20px;
                    }
                }
            }
        }
    }
}

@media (max-width: 954px) {
    .section-header {

        .header {

            .header-body {
                margin: 15px 0;

                &:before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background-color: #F7FFFF;
                    z-index: 15;
                }

                .logo {
                    z-index: 22;
                    display: flex;
                    align-items: center;

                    img {
                        // height: 60px;
                        // width: 93px;
                        height: 39px;
                        width: 72px;
                    }
                }

                .header-content {
                    background-color: #F7FFFF;
                    position: absolute;
                    width: 100%;
                    display: none;
                    z-index: 1;
                    left: 0;
                    top: -300px;
                    display: block;
                    padding-left: 20px;
                    overflow: auto;
                    transition: 0.5s;

                    &.active {
                        top: 69px;
                        transition: 0.5s;

                    }

                    .header-list-sections {
                        flex-direction: column;
                        margin: 5px 0 31px 0;



                        .header-list-section {
                            padding: 0px;

                            a {
                                &:hover {
                                    &::after {
                                        display: none;
                                    }
                                }
                            }

                            &:not(:last-child) {
                                margin-bottom: 15px;
                            }
                        }
                    }

                    .header-list-icons {
                        margin-bottom: 31px
                    }

                    .header-btn {
                        margin-bottom: 30px;
                    }
                }
            }
        }
    }
}