.babysitter-section {
    background-color: #F7FFFF;

    .babysitter-body {
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 100px 20px;


        .text-container {
            max-width: 480px;
            position: relative;

            .family-img {
                position: absolute;
                top: 165px;
                left: 100px;
            }

            .heart-img {
                position: absolute;
                top: -100px;
                left: 500px;
            }

            .cloud-img {
                position: absolute;
                top: -160px;
                left: 0;
                transform: rotate(-24.14deg);
            }

            .butterfly-img {
                position: absolute;
                top: 200px;
                left: 530px;
            }

            h2 {
                span {
                    position: relative;
                    z-index: 1;

                    &::after {
                        display: block;
                        position: absolute;
                        left: -3px;
                        top: 63px;
                        content: '';
                        width: calc(100%);
                        height: 15px;
                        background-color: #FFD583;
                        ;
                        z-index: -1;
                        border-radius: 100px;
                    }
                }

                img {
                    position: absolute;
                    top: 7px;
                    right: -15px;
                    transform: rotate(70.66deg);
                }
            }
        }

        .form-container {
            position: relative;
            max-width: 470px;
            background: #FFFFFF;
            box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.08);
            border-radius: 10px;


            .form-body {
                margin: 40px;

                h3 {
                    text-align: center;
                    margin-bottom: 30px;
                }

                .call-form {
                    position: relative;

                    .errorMessage {
                        font-size: 14px;
                        height: 19px;
                        margin: 0 0 0 15px;
                        color: red;
                        line-height: 19.1px;
                    }

                    input {
                        max-width: 90%;
                    }

                    div {
                        align-items: flex-start;
                        display: flex;
                        margin: 10px 0;

                        p {
                            margin: 0;

                        }
                    }

                    p {
                        font-size: 14px;
                        line-height: 19px;
                    }
                }
            }
        }
    }
}



@media (max-width: 1000px) {
    .babysitter-section {

        .babysitter-body {
            padding: 70px 20px;
            flex-direction: column;


            .text-container {
                min-width: 100%;
                margin-bottom: 20px;

                .heart-img {
                    display: none;
                }

                .butterfly-img {
                    display: none;
                }

                .cloud-img {
                    display: none;
                }

                .family-img {
                    display: none;
                }

                h2 {
                    span {
                        position: relative;
                        z-index: 1;

                        &::after {
                            display: block;
                            position: absolute;
                            left: -3px;
                            top: 41px;
                            content: '';
                            width: calc(100%);
                            height: 10px;
                            background-color: #FFD583;
                            ;
                            z-index: -1;
                            border-radius: 100px;
                        }
                    }

                    img {
                        position: absolute;
                        top: -2px;
                        right: -16px;
                        transform: rotate(70.66deg);
                    }
                }
            }

            .form-container {
                position: relative;
                max-width: 444px;
                background: #FFFFFF;
                box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.08);
                border-radius: 10px;
                margin: 0px auto;

                .form-body {
                    margin: 30px 20px;
                }
            }
        }
    }
}

@media (max-width: 1130px) {

    .babysitter-section {

        .babysitter-body {



            .text-container {
                .heart-img {
                    left: 430px;
                }

                .butterfly-img {
                    left: 430px;
                }
            }
        }
    }
}