.price-section {
    .price-body {
        .price-header {
            margin: 120px 0 50px 0;

            h2 {
                span {
                    position: relative;
                    z-index: 1;

                    &::after {
                        display: block;
                        position: absolute;
                        left: -3px;
                        top: 63px;
                        content: '';
                        width: calc(103%);
                        height: 15px;
                        background-color: #FFD583;
                        ;
                        z-index: -1;
                        border-radius: 100px;
                    }
                }

                img {
                    position: absolute;
                    top: -13px;
                    left: 125px;
                    transform: rotate(45.66deg);
                }
            }
        }

        .price-cards {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            grid-column-gap: 30px;
            margin: 70px 0 70px 0;



        }
    }
}

@media (max-width: 1000px) {

    .price-section {
        .price-body {
            .price-header {
                margin: 70px 0 30px 0;

                h2 {
                    span {
                        position: relative;
                        z-index: 1;

                        &::after {
                            display: block;
                            position: absolute;
                            left: -3px;
                            top: 43px;
                            content: '';
                            width: calc(107%);
                            height: 10px;
                            background-color: #FFD583;
                            ;
                            z-index: -1;
                            border-radius: 100px;
                        }


                    }

                    img {
                        position: absolute;
                        top: -20px;
                        left: 84px;
                        transform: rotate(45.66deg);
                    }
                }
            }

            .price-cards {
                display: grid;
                grid-template-columns: repeat(3, 1fr);
                grid-column-gap: 30px;
                grid-row-gap: 30px;
                margin: 70px 0 70px 0;

                @media (max-width: 888px) {
                    grid-template-columns: repeat(1, 1fr);
                    align-items: center;
                }
            }
        }
    }
}