%arrow {
    position: absolute;
    border-radius: 50%;
    background-color: #D0F8FC;
    height: 48px;
    width: 48px;
    z-index: 3;
    top: 50%;
    text-align: center;

    &:hover {
        cursor: pointer;
        box-shadow: 0px 2px 16px rgba(0, 128, 141, 0.37);
    }
}

.arrow-next {
    @extend %arrow;
    right: 22px;
}

.arrow-prev {
    @extend %arrow;
    left: 22px;
}