    .slider-card {
        display: flex;
        flex-direction: column;


        .nanny-photo {
            padding-bottom: 20px;
            transition: 0.3s;
            width: 270px;
            height: 350px;

            img {
                width: 100%;
                height: 100%;
                border-radius: 10px;
            }

            &:hover {
                filter: drop-shadow(0px 8px 20px rgba(37, 194, 210, 0.25));
                cursor: pointer;
            }
        }

        p {
            max-width: 70%;
        }
    }