@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;500;700&display=swap');

@font-face {
  font-family: VAG World;
  src: url(fonts/vag.ttf);
}

body {
  font-family: 'Nunito', sans-serif;
  font-weight: 400;
  font-size: 34px;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}





.App {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 100%;
  position: relative;
}

.container {
  max-width: 1200px;
  padding: 0 20px;
  margin: 0 auto;
  box-sizing: border-box;
  position: relative;

}

.cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 30px;
  grid-row-gap: 30px;
  justify-items: center;
  margin: 0 auto;
}

ul {
  padding: 0;
  margin: 0;
}

p {

  font-size: 16px;
  line-height: 26px;
}

.slick-track {
  display: flex;
}


h2 {
  font-size: 48px;
  font-weight: 700;
  color: #141414;
  line-height: 53.93px;
  margin: 0;
  font-family: "VAG World";
}

h3 {
  font-size: 28px;
  font-weight: 700;
  color: #141414;
  line-height: 32.74px;
  margin: 0;
  font-family: "VAG World";
}

h4 {
  font-size: 24px;
  font-weight: 700;
  color: #141414;
  line-height: 32.74px;
  margin: 0;
}






@media (max-width: 1000px) {
  h2 {
    font-size: 32px;
    font-weight: 700;
    color: #141414;
    line-height: 35.95px;
    margin: 0;
    font-family: "VAG World";
  }

  h3 {
    font-size: 24px;
    font-weight: 700;
    color: #141414;
    line-height:
      26.96px;
    margin: 0;
    font-family: "VAG World";
  }

  h4 {
    font-size: 20px;
    font-weight: 700;
    color: #141414;
    line-height:
      27.28px;
    margin: 0;
  }

  p {

    font-size: 14px;
    line-height: 22px;
  }
}