@mixin button($border, $bg, $bg-h, $bs, $bs-h, $bg-a) {
    color: white;
    border-radius: 100px;
    padding: 14px 40px;
    border: $border;
    background: $bg;
    box-shadow: $bs;
    font-weight: 700;
    font-family: 'Nunito', sans-serif;


    &:hover {
        background: $bg-h;
        box-shadow: $bs-h;
        cursor: pointer;
    }

    &:active {
        background: $bg-a;
        border-radius: 100px;
    }

}

;


.buttonPrimary1 {
    @include button(rgba(53, 211, 227, 0.47),
        linear-gradient(83.08deg, #02A6CA 11.02%, #00D2E7 86.53%),
        linear-gradient(83.51deg, #02A6CA -23.2%, #00D2E7 54.33%),
        0px 8px 20px rgba(53, 211, 227, 0.47),
        0px 8px 14px rgba(53, 211, 227, 0.69),
        linear-gradient(84.5deg, #02A6CA 3.01%, #00D2E7 104.65%))
}

;

.buttonPrimary2 {
    @include button(rgba(255, 144, 40, 0.44),
        linear-gradient(83.93deg, #FFAD61 32.87%, #FFCC6A 98.66%),
        linear-gradient(84.78deg, #FFAD61 -41.21%, #FFCC6A 98.51%),
        0px 8px 20px rgba(255, 144, 40, 0.44),
        0px 8px 20px rgba(255, 144, 40, 0.65),
        linear-gradient(83.93deg, #FFAD61 32.87%, #FFCC6A 98.66%))
}

;

.buttonSecondary {
    @include button(rgba(189, 40, 139, 0.49),
        linear-gradient(85.31deg, #B51D81 -4.66%, #EC67BF 96.19%),
        linear-gradient(84.99deg, #D2339C -8.08%, #F479CB 106.17%),
        0px 8px 20px rgba(189, 40, 139, 0.49),
        0px 8px 20px rgba(189, 40, 139, 0.67),
        linear-gradient(85.31deg, #B51D81 -4.66%, #EC67BF 96.19%))
}