.goals-section {
    width: 100%;

    .bottom-section-title {
        margin-bottom: 50px;
        position: relative;
        z-index: 2;

        span {
            position: relative;

            &::after {
                display: block;
                position: absolute;
                left: -3px;
                top: 63px;
                content: '';
                width: calc(110%);
                height: 15px;
                background-color: #FFD583;
                ;
                z-index: -1;
                border-radius: 100px;
            }
        }

        img {
            position: absolute;
            top: -13px;
            left: 277px;
            transform: rotate(70.66deg);
        }
    }

    .top-sections {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 30px;
        grid-row-gap: 30px;
        align-items: center;
        margin: 90px 0 120px 0;



        .goals-image {
            // max-height: 470px;
            // max-width: 518px;
            position: relative;

            &::after {
                content: '';
                position: absolute;
                left: 71.49%;
                right: -2.77%;
                top: -2.32%;
                background: #DF51AF;
                opacity: 0.6;
                transform: matrix(1, 0, 0, 1, 0, 0);
                bottom: 94.21%;
            }

            &::before {
                content: '';
                position: absolute;
                height: inherit;
                left: -4.49%;
                right: 70.21%;
                top: 87.71%;
                bottom: 4.18%;
                background: #62E7F4;
                opacity: 0.6;
                transform: matrix(0.87, 0.5, -0.5, 0.87, 0, 0);

            }

            img {
                height: 100%;
                width: 100%;
            }


        }

        .top-section-second {
            .top-section-title {
                margin-bottom: 50px;
                font-family: "VAG World";

                span {
                    position: relative;

                    &::after {
                        display: block;
                        position: absolute;
                        left: -3px;
                        top: 63px;
                        content: '';
                        width: calc(100%);
                        height: 15px;
                        background-color: #FFD583;
                        ;
                        z-index: -1;
                        border-radius: 100px;
                    }
                }

                img {
                    position: absolute;
                    left: 7px;
                    top: 7px;
                    transform: rotate(70.66deg);
                }
            }

            .italic-text {
                font-style: italic;
                font-weight: 600;

            }
        }
    }

    .companies-goals {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-column-gap: 30px;
        grid-row-gap: 20px;
        margin-bottom: 120px;

        .card-goals {
            display: flex;
            max-width: 370px;

            // img {
            //     width: 10;
            //     height: ;
            // }

            .card-text {
                margin-left: 30px;
            }
        }

    }
}

